<template>
  <!--  -->
  <div>
    <el-dialog
      :title="edit_dig_title"
      :close-on-click-modal="false"
      :visible.sync="edit_dig_visible"
      :fullscreen="true"
      @close="handleEditDigClose"
    >
      <el-form ref="job_edit_form" :model="form" :rules="rules" label-width="150px" size="mini">
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="name">
              <el-input v-model="form.name" placeholder=""></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="Cron" prop="cron">
              <el-input v-model="form.cron" placeholder="Cron"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="/" prop="protocol">
              <el-select v-model="form.protocol" class="handle-select mr10">
                <el-option label="http/get" value="http"></el-option>
                <el-option label="https/get" value="https"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="uri">
              <el-input
                v-model="form.uri"
                placeholder="(URI), http://localhost:8080/bills/check /bills/check "
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="httpParam">
              <el-input
                v-model="form.httpParam"
                placeholder="name=value|name1=value1|name2=value2"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="Header" prop="httpHeaderParam">
              <el-input
                v-model="form.httpHeaderParam"
                placeholder="Headername=value|name1=value1|name2=value2"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="httpSign">
              <el-select v-model="form.httpSign" class="handle-select mr10">
                <el-option label="" value="0"></el-option>
                <el-option label="" value="1"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="()" prop="timeout">
              <el-input-number v-model="form.timeout" :min="10" :max="300"></el-input-number>
              <span style="font-size: 13px;color: #999;">&nbsp;&nbsp;10 - 300</span>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="retryCount">
              <el-input-number v-model="form.retryCount" :min="0" :max="10"></el-input-number>
              <span style="font-size: 13px;color: #999;">&nbsp;&nbsp;0 </span>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="()" prop="retryWaitTime">
              <el-input-number v-model="form.retryWaitTime" :min="0" :max="1000"></el-input-number>
              <span style="font-size: 13px;color: #999;">&nbsp;&nbsp;0 </span>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="failTakeover">
              <el-select v-model="form.failTakeover" class="handle-select mr10">
                <el-option label="" value="1"></el-option>
                <el-option label="" value="0"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="misfire()" prop="misfireThreshold">
              <el-input-number v-model="form.misfireThreshold" :min="0" :max="1800"></el-input-number>
              <span style="font-size: 13px;color: #999;">&nbsp;&nbsp;0 misfire</span>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-row>
              <el-col :span="12">
                <el-form-item label="" prop="subJobIds">
                  <el-select v-model="form.subJobIds" multiple placeholder=",">
                    <el-option
                      v-for="item in subjob_options"
                      :key="item.id"
                      :label="item.name"
                      :value="item.id"
                    ></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item
                  v-if="form.subJobIds&&form.subJobIds.length>0"
                  label=""
                  prop="subJobScheduleStrategy"
                >
                  <el-select
                    v-model="form.subJobScheduleStrategy"
                    placeholder=""
                    class="handle-select mr10"
                  >
                    <el-option label="" value="0"></el-option>
                    <el-option label="" value="1"></el-option>
                    <el-option label="" value="2"></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
            </el-row>
          </el-col>
          <el-col :span="11">
            <el-form-item label="" prop="alarmEmail">
              <el-input v-model="form.alarmEmail" placeholder="|;"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="1">
            &nbsp;
            <el-button
              @click="handleUserMailSelectorDig"
              type="primary"
              icon="el-icon-user-solid"
              circle
              size="mini"
            ></el-button>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="executorSelectStrategy">
              <el-select
                v-model="form.executorSelectStrategy"
                placeholder=""
                class="handle-select mr10"
              >
                <el-option label=" " value="random"></el-option>
                <el-option label=" " value="round"></el-option>
                <el-option label=" " value="weight_random"></el-option>
                <el-option label=" " value="weight_round"></el-option>
                <el-option label=" " value="sharding"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="remark">
              <el-input v-model="form.remark" placeholder=""></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item
              v-if="form.executorSelectStrategy=='sharding'"
              label=""
              prop="shardingCount"
            >
              <el-input-number v-model="form.shardingCount" :min="1" :max="10000"></el-input-number>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item
              v-if="form.executorSelectStrategy=='sharding'"
              label=""
              prop="shardingParam"
            >
              <el-input
                v-model="form.shardingParam"
                placeholder="|340101|340102|340103|340104"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <fieldset style="border-color: #999;border-width: 1px;">
          <legend style="font-weight:bold;font-size: 12px;">
            <span></span>
          </legend>
          <el-row>
            <el-col :span="24">
              <el-row v-for="(node, index) in form.executors" :key="'node.' + index + '.key2'">
                <el-col :span="10">
                  <el-form-item
                    label=""
                    :prop="`executors[${index}].address`"
                    :rules="[{ required: true, message: '', trigger: 'blur'}]"
                  >
                    <el-input v-model="node.address" placeholder=" 'IP:'"></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="5">
                  <el-form-item
                    label=""
                    prop="weight"
                    v-if="form.executorSelectStrategy=='weight_random'||form.executorSelectStrategy=='weight_round'"
                  >
                    <el-input-number v-model="node.weight" :min="1" :max="100"></el-input-number>
                  </el-form-item>
                </el-col>
                <el-col :span="5">
                  <el-form-item label="" prop="weight">
                    <el-select v-model="node.status" class="handle-select mr10">
                      <el-option label="" value="1"></el-option>
                      <el-option label="" value="0"></el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
                <el-col :span="4">
                  <el-form-item>
                    <el-button
                      v-if="index==0"
                      @click="handleAddExecutor()"
                      type="success"
                      icon="el-icon-plus"
                    ></el-button>
                    <el-button
                      v-if="index>0"
                      @click="handleDelExecutor(index)"
                      type="danger"
                      icon="el-icon-delete"
                    ></el-button>
                  </el-form-item>
                </el-col>
              </el-row>
            </el-col>
          </el-row>
        </fieldset>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="handleHelp" type="warning" icon="el-icon-question"></el-button>&nbsp;&nbsp;&nbsp;&nbsp;
        <el-button @click="edit_dig_visible = false" icon="el-icon-close"> </el-button>
        <el-button type="primary" @click="saveEdit" icon="el-icon-check"> </el-button>
      </span>
    </el-dialog>
    <user-mail-selector ref="user_mail_selector" @refreshList="handleUserMailSelected"></user-mail-selector>
    <job-edit-help ref="job_edit_help"></job-edit-help>
  </div>
</template>

<script>
import jobApi from "@/api/JobApi";
import userApi from "@/api/UserApi";
import jobEditHelp from "@/views/job/JobEditHelp";
import userMailSelector from "@/views/job/UserMailSelector";

export default {
  name: "JobEdit",
  components: {
    jobEditHelp,
    userMailSelector
  },
  data() {
    return {
      edit_dig_visible: false,
      subjob_options: [],
      edit_dig_title: "",
      form: this.emptyForm(),
      rules: this.validRules()
    };
  },
  methods: {
    initPage(id) {
      if (id) {
        this.edit_dig_title = "";
        jobApi.getJob(id).then(res => {
          this.form = res.data;
          this.form.httpSign = res.data.httpSign + "";
          this.form.failTakeover = res.data.failTakeover + "";
          this.form.subJobScheduleStrategy =
            res.data.subJobScheduleStrategy + "";
          for (let index in this.form.executors) {
            let node = this.form.executors[index];
            node.status = node.status + "";
          }
          jobApi.getSubJobSelections(id).then(res => {
            this.subjob_options = res.data;
          });
          this.edit_dig_visible = true;
        });
      } else {
        jobApi.getSubJobSelections('').then(res => {
          this.subjob_options = res.data;
          this.edit_dig_title = "";
          this.edit_dig_visible = true;
        });
      }
    },
    emptyForm() {
      return {
        name: "", // 
        cron: "", // cron 
        protocol: "http", //  http / https
        uri: "", // 
        remark: "", // 
        status: 1, //  0 1
        creator: "", // 
        preJobId: "", // ID
        timeout: 60, // 
        retryCount: 0, // 
        retryWaitTime: "", // 
        failTakeover: "1", //  0 1
        misfireThreshold: 0, // 
        executorSelectStrategy: "", //    
        httpParam: "", // http
        httpHeaderParam: "", // http
        httpSign: "0", // 
        shardingCount: 0, // 
        shardingParam: "", // 
        alarmEmail: "", // 
        subJobScheduleStrategy: "0", // JOB 0 1 2
        subJobIds: [], // JOB
        executors: [
          {
            address: "",
            weight: 0,
            status: "1"
          }
        ] // 
      };
    },
    validRules() {
      var validateCron = (rule, value, callback) => {
        if (value === "") {
          callback(new Error("Cron"));
        } else {
          jobApi.validateCron(value).then(res => {
            let ok = res.data;
            if (ok) {
              callback();
            } else {
              callback(new Error("Cron"));
            }
          });
        }
      };
      return {
        name: [{ required: true, message: "", trigger: "blur" }],
        cron: [{ validator: validateCron, required: true, trigger: "blur" }],
        protocol: [
          { required: true, message: "", trigger: "blur" }
        ],
        uri: [
          { required: true, message: "", trigger: "blur" }
        ],
        executorSelectStrategy: [
          { required: true, message: "", trigger: "blur" }
        ],
        failTakeover: [
          { required: true, message: "", trigger: "blur" }
        ],
        timeout: [
          { required: true, message: "", trigger: "blur" }
        ]
      };
    },
    handleAddExecutor() {
      this.form.executors.push({
        address: "",
        weight: 0,
        status: "1"
      });
    },
    handleDelExecutor(index) {
      this.form.executors.splice(index, 1);
    },
    handleHelp() {
      this.$refs.job_edit_help.initPage();
    },
    handleUserMailSelectorDig() {
      this.$refs.user_mail_selector.initPage(this.form.alarmEmail);
    },
    handleUserMailSelected(val) {
      this.form.alarmEmail = val
    },
    // 
    saveEdit() {
      this.$refs.job_edit_form.validate(valid => {
        if (valid) {
          if (this.form.executorSelectStrategy != "sharding") {
            this.form.shardingCount = 0;
            this.form.shardingParam = "";
          }
          if (this.form.httpParam) {
            let p = this.form.httpParam;
            let ps = p.split("|");
            for (let i in ps) {
              let pss = ps[i].split("=");
              if (pss.length != 2) {
                this.$message.error(
                  " 'key=value' |"
                );
                return;
              }
            }
          }
          if (this.form.httpHeaderParam) {
            let p = this.form.httpHeaderParam;
            let ps = p.split("|");
            for (let i in ps) {
              let pss = ps[i].split("=");
              if (pss.length != 2) {
                this.$message.error(
                  "Header 'key=value' |"
                );
                return;
              }
            }
          }
          if (this.form.shardingCount > 0) {
            if (this.form.shardingParam) {
              let p = this.form.shardingParam;
              let ps = p.split("|");
              if (ps.length != this.form.shardingCount) {
                this.$message.error(
                  "|"
                );
                return;
              }
            }
          }
          if (this.form.alarmEmail) {
            let p = this.form.alarmEmail;
            let ps = p.split("|");
            for (let i in ps) {
              var reg = new RegExp(/^\S+@\S+\.\S{2,}$/);
              if (!reg.test(ps[i])) {
                this.$message.error("");
                return;
              }
            }
          }

          for (let index in this.form.executors) {
            let node = this.form.executors[index];
            var reg = new RegExp(
              /^(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\:([0-9]|[1-9]\d{1,3}|[1-5]\d{4}|6[0-5]{2}[0-3][0-5])$/
            );
            if (!reg.test(node.address)) {
              this.$message.error(
                "" +
                  (parseInt(index) + 1) +
                  "  'IP:'  "
              );
              return;
            }
          }
          for (let i in this.form.executors) {
            let node = this.form.executors[i];
            for (let j in this.form.executors) {
              if (i != j) {
                let node2 = this.form.executors[j];
                if (node.address == node2.address) {
                  this.$message.error(
                    "" +
                      (parseInt(i) + 1) +
                      " "
                  );
                  return;
                }
              }
            }
          }
          for (let index in this.form.executors) {
            let node = this.form.executors[index];
            node.status = parseInt(node.status);
          }

          this.form.failTakeover = parseInt(this.form.failTakeover);
          this.form.httpSign = parseInt(this.form.httpSign);
          this.form.subJobScheduleStrategy = parseInt(
            this.form.subJobScheduleStrategy
          );
          if (this.form.id) {
            jobApi.putJob(this.form).then(res => {
              this.edit_dig_visible = false;
              this.$emit("refreshList");
              this.$message.success(``);
            });
          } else {
            this.form.creator = this.$store.getters.getUserName;
            jobApi.postJob(this.form).then(res => {
              this.edit_dig_visible = false;
              this.$emit("refreshList");
              this.$message.success(``);
            });
          }
        } else {
          console.log("error submit!!");
          return;
        }
      });
    },
    handleEditDigClose() {
      this.form = this.emptyForm();
      this.$refs.job_edit_form.resetFields();
    }
  }
};
</script>