<template>
  <!--  -->
  <el-dialog
    width="70%"
    :title="edit_dig_title"
    :close-on-click-modal="false"
    :visible.sync="edit_dig_visible"
  >
    <div style="overflow: auto;">
      <table class="view_table">
        <tr>
          <td class="view_table_label"></td>
          <td class="view_table_td">{{job.name}}</td>
          <td class="view_table_label">Cron</td>
          <td class="view_table_td">{{job.cron}}</td>
        </tr>
        <tr>
          <td class="view_table_label">/</td>
          <td class="view_table_td">{{job.protocol}}/get</td>
          <td class="view_table_label"></td>
          <td class="view_table_td">{{job.uri}}</td>
        </tr>
        <tr>
          <td class="view_table_label"></td>
          <td class="view_table_td">{{job.httpParam}}</td>
          <td class="view_table_label">Header</td>
          <td class="view_table_td">{{job.httpHeaderParam}}</td>
        </tr>
        <tr>
          <td class="view_table_label"></td>
          <td class="view_table_td">
            <span v-if="job.HttpSign==1"></span>
            <span v-else></span>
          </td>
          <td class="view_table_label"></td>
          <td class="view_table_td">
            <span v-if="job.timeout>0">{{job.timeout}} </span>
            <span v-else>&nbsp;-&nbsp;</span>
          </td>
        </tr>
        <tr>
          <td class="view_table_label"></td>
          <td class="view_table_td">
            <span v-if="job.retryCount>0">{{job.retryCount}} </span>
            <span v-else>&nbsp;-&nbsp;</span>
          </td>
          <td class="view_table_label"></td>
          <td class="view_table_td">
            <span v-if="job.retryWaitTime>0">{{job.retryWaitTime}} </span>
            <span v-else>&nbsp;-&nbsp;</span>
          </td>
        </tr>
        <tr>
          <td class="view_table_label"></td>
          <td class="view_table_td">
            <span v-if="job.failTakeover==0"></span>
            <span v-if="job.failTakeover==1"></span>
          </td>
          <td class="view_table_label">misfire</td>
          <td class="view_table_td">
            <span v-if="job.misfireThreshold>0">{{job.misfireThreshold}} </span>
            <span v-else>&nbsp;-&nbsp;</span>
          </td>
        </tr>
        <tr v-if="job.subJobDisplay">
          <td class="view_table_label"></td>
          <td class="view_table_td" colspan="3">
            {{job.subJobDisplay}}
            &nbsp;&nbsp;&nbsp;&nbsp;
            (
            <span v-if="job.subJobScheduleStrategy==0"></span>
            <span v-else-if="job.subJobScheduleStrategy==1"></span>
            <span v-else-if="job.subJobScheduleStrategy==2"></span>
            <span v-else></span>)
          </td>
        </tr>
        <tr>
          <td class="view_table_label"></td>
          <td class="view_table_td" colspan="3">
            <span v-if="job.executorSelectStrategy=='random'"> </span>
            <span v-if="job.executorSelectStrategy=='round'"> </span>
            <span v-if="job.executorSelectStrategy=='weight_random'"> </span>
            <span v-if="job.executorSelectStrategy=='weight_round'"> </span>
            <span v-if="job.executorSelectStrategy=='sharding'"> </span>
          </td>
        </tr>
        <tr v-if="job.executorSelectStrategy=='sharding'">
          <td class="view_table_label"></td>
          <td class="view_table_td" colspan="3">
            {{job.shardingCount}}
            &nbsp;&nbsp; &nbsp;&nbsp;
            {{job.shardingParam}}
          </td>
        </tr>
        <tr>
          <td class="view_table_label"></td>
          <td class="view_table_td" colspan="3">
            <div v-for="(node, index) in job.executors" :key="'node.' + index + '.key3'">
              {{index+1}}
              : {{node.address}} &emsp;
              :
              <span
                v-if="node.status==1"
                style="color:#67C23A;"
              ></span>
              <span v-if="node.status==0" style="color:#F56C6C;"></span>
              &emsp;
              <span v-if="displayWeight">:{{node.weight}}</span>
            </div>
          </td>
        </tr>
        <tr>
          <td class="view_table_label"></td>
          <td class="view_table_td">{{job.alarmEmail}}</td>
          <td class="view_table_label"></td>
          <td class="view_table_td">{{job.remark}}</td>
        </tr>
      </table>
    </div>
    <span slot="footer" class="dialog-footer">
      <el-button @click="edit_dig_visible = false"> </el-button>
    </span>
  </el-dialog>
</template>

<script>
import jobApi from "@/api/JobApi";

export default {
  name: "JobView",
  data() {
    return {
      edit_dig_visible: false,
      edit_dig_title: "",
      displayWeight: false,
      job: {}
    };
  },
  methods: {
    initPage(id) {
      jobApi.getJob(id).then(res => {
        this.job = res.data;
        this.job.failTakeover = res.data.failTakeover + "";
        this.job.sendTraceId = res.data.sendTraceId + "";
        if (
          this.job.executorSelectStrategy == "weight_random" ||
          this.job.executorSelectStrategy == "weight_round"
        ) {
          this.displayWeight = true;
        }
      });
      this.edit_dig_visible = true;
    }
  }
};
</script>
<style type="text/css">
.view_table {
  width: 100%;
  font-family: verdana, arial, sans-serif;
  font-size: 11px;
  color: #333333;
  border-width: 1px;
  border-color: #dcdfe6;
  border-collapse: collapse;
}
.view_table_label {
  width: 12%;
  border-width: 1px;
  padding: 8px;
  border-style: solid;
  border-color: #dcdfe6;
  background-color: #ffffff;
  text-align: right;
  color: #999;
  font-weight: bold;
  font-size: 12px;
}
.view_table_td {
  width: 40%;
  border-width: 1px;
  padding: 8px;
  border-style: solid;
  border-color: #dcdfe6;
  background-color: #ffffff;
  font-size: 13px;
}
</style>