<template>
  <div class="table">
    <div class="container">
      <div class="handle-box">
        <el-input
          size="small"
          v-model="search_job_name"
          clearable
          @clear="handleSearch"
          placeholder=""
          class="handle-input"
        ></el-input>
        <el-input
          size="small"
          clearable
          @clear="handleSearch"
          v-model="search_job_creator"
          placeholder=""
          class="handle-input"
        ></el-input>
        <el-select
          size="small"
          v-model="search_job_status"
          class="handle-select"
          placeholder=""
        >
          <el-option label value></el-option>
          <el-option label="" value="1"></el-option>
          <el-option label="" value="0"></el-option>
        </el-select>
        <el-button size="small" type="primary" icon="el-icon-search" @click="handleSearch"></el-button>
        <el-button size="small" type="primary" icon="el-icon-plus" @click="handleAdd"></el-button>
      </div>
      <el-table
        :data="table_date"
        border
        style="width: 100%"
        :row-style="{height:'36px'}"
        :header-row-style="{height:'36px'}"
        :cell-style="{padding:'1px'}"
      >
        <el-table-column prop="name" label="" width="250" align="center"/>
        <el-table-column prop="cron" label="Cron" width="170" align="center"/>
        <el-table-column label="" width="65" align="center">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.status==1" size="small" type="success"></el-tag>
            <el-tag v-if="scope.row.status==0" size="small" type="danger"></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="/" width="200" align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.executorCount==0"></span>
            <span v-else>{{scope.row.executorCount}} /
            <span v-if="scope.row.executorSelectStrategy=='random'"></span>
            <span v-if="scope.row.executorSelectStrategy=='round'"></span>
            <span v-if="scope.row.executorSelectStrategy=='weight_random'"></span>
            <span v-if="scope.row.executorSelectStrategy=='weight_round'"></span>
            <span v-if="scope.row.executorSelectStrategy=='sharding'"></span>
            </span>
          </template>
        </el-table-column>
        <el-table-column prop="creator" width="80" label="" align="center"/>
        <el-table-column width="140" label="" align="center" :formatter="createTimeTimeFmt"/>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <el-button v-if="scope.row.status==1" size="mini" type="text" @click="handleView(scope.row.id)"></el-button>
            <el-button
              size="mini"
              v-if="scope.row.status==0"
              @click="handleStatus(scope.row.id,scope.row.status)"
              type="text"
            ></el-button>
            <el-button
              size="mini"
              v-if="scope.row.status==1"
              @click="handleStatus(scope.row.id,scope.row.status)"
              type="text"
            ></el-button>
            <el-button size="mini" type="text" @click="handleEdit(scope.row.id)"></el-button>
            <el-button v-if="scope.row.status==1" size="mini" type="text" @click="handleLaunch(scope.row.id)"></el-button>
            <el-button size="mini" type="text" @click="handleDelete(scope.row.id)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="pagination">
        <el-pagination
          background
          @current-change="handleCurrentChange"
          @size-change="handleSizeChange"
          :page-sizes="[10,20, 50, 100]"
          :page-size="page_size"
          layout="total, sizes, prev, pager, next, jumper"
          :total="table_data_total"
          :current-page.sync="page_num"
        ></el-pagination>
      </div>
    </div>

    <job-view ref="job_view"></job-view>
    <job-edit ref="job_edit" @refreshList="getData"></job-edit>
  </div>
</template>

<script>
import jobApi from "@/api/JobApi";
import { formatDate } from "@/utils/date";
import jobView from "@/views/job/JobView";
import jobEdit from "@/views/job/JobEdit";

export default {
  name: "JobList",
  components: {
    jobEdit,
    jobView
  },
  data() {
    return {
      search_job_name: "",
      search_job_creator: "",
      search_job_status: "",
      table_date: [],
      table_data_total: 0,
      page_num: 1,
      page_size: 10
    };
  },
  created() {
    this.getData();
  },
  methods: {
    // 
    handleCurrentChange(val) {
      this.page_num = val;
      this.getData();
    },
    // 
    handleSizeChange(val) {
      this.page_size = val;
      this.getData();
    },
    // 
    handleSearch() {
      this.page_num = 1;
      this.getData();
    },
    // 
    handleSelectionChange(val) {
      this.table_selections = val;
    },
    // 
    getData() {
      jobApi
        .getJobs({
          page_num: this.page_num,
          page_size: this.page_size,
          name: this.search_job_name,
          creator: this.search_job_creator,
          status: this.search_job_status
        })
        .then(res => {
          this.table_date = res.data;
          this.table_data_total = res.total;
        });
    },
    // 
    handleAdd() {
      this.$refs.job_edit.initPage();
    },
    // 
    handleEdit(id) {
      this.$refs.job_edit.initPage(id);
    },
    // 
    handleView(id) {
      this.$refs.job_view.initPage(id);
    },
    // 
    handleStatus(id, status) {
      if (status == 0) {
        status = 1;
        jobApi.updateStatus(id, status).then(res => {
          this.getData();
        });
      } else {
        status = 0;
        this.$confirm(",, ?", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        })
          .then(() => {
            jobApi.updateStatus(id, status).then(res => {
              this.getData();
            });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: ""
            });
          });
      }
    },
    handleDelete(id) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          jobApi.deleteJob(id, status).then(res => {
            this.getData();
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    handleLaunch(id) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          jobApi.launchJob(id, status).then(res => {
            this.$message({
              type: "success",
              message: " '' "
            });
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    createTimeTimeFmt(row, column) {
      let date = new Date(row.createTime);
      return formatDate(date, "yyyy-MM-dd hh:mm");
    }
  }
};
</script>

<style scoped>
.handle-box {
  margin-bottom: 10px;
}
.handle-input {
  width: 200px;
  display: inline-block;
  margin-right: 10px;
}
.handle-select {
  width: 110px;
  display: inline-block;
  margin-right: 10px;
}
.del-dialog-cnt {
  font-size: 16px;
  text-align: center;
}
</style>
